import React from "react";

import logo from "../images/transport.png"
// import drop from "../images/down.png"

const Navbar = (props:any) =>{

    return (
        <div>
             <div className="navbar w-full py-4">
                        
                <img className="h-20 inline-block" src={logo} href="/" />
                <input type="checkbox" id="checkbox_toggle" />
                <label for="checkbox_toggle" className="hamburger">&#9776;</label>
                  <ul className="navbar-ul">
                    <li><a href="/">HOME</a></li>
                    <li><a href="#">SERVICES</a> 
                        <ul className="drop-down border-indigo-800 border-2  background">
                          <li><a href="/private">Private Coach Hire</a></li>
                          <li><a href="/corp">Corporate Coach Hire</a></li>
                          <li><a href="/airport">Airport Transfers</a></li>
                          <li><a href="/stations">Station Transfers</a></li>
                          <li><a href="/event">Events Coach Hire</a></li>
                          <li><a href="/europe">UK/Europe Tours</a></li>                                
                        </ul>
                    </li>
                    <li><a href="/gallery">FLEET</a></li>         
                    <li><a href="/about">ABOUT</a></li>
                    <li><a href="/contact">CONTACT</a></li>
                  </ul>
                  <a type="button" href="/contact" className="button-nav text-white font-bold "> BOOK NOW</a>
                       

            </div> 
            
        </div>
        )
    }
 export default Navbar;